// or link copy to index.html
// eslint-disable-next-line
import { Button } from 'react-bootstrap'; // import Button from 'react-bootstrap/Button';
// eslint-disable-next-line
import {Navbar, Nav, NavDropdown, Form, FormControl, Container} from 'react-bootstrap';
import bg from './img/bg1.jpg';
//import bg from './img/shoes_banner.jpg';
//import shoe1 from './img/shoe1.jpg';
