import data from './data.js';
import Detail from './routes/Detail';
import axios from 'axios';
// eslint-disable-next-line
function App() {
  // eslint-disable-next-line
  let [shoes,setShoes] = useState(data);
  console.log('@@ shoes=',shoes);
  console.log('@@ shoes[0]=',shoes[0]);
  let [i,setI] = useState(2); //  2 .  i is index of shoes
  console.log('@@ current data position i=',i);
  //      2(data2.json)  )

  // last data position
  let [last,setLast] = useState(3); //   .  3 .  last is index of shoes

  let navigate = useNavigate(); // useNavigate() is a hook for react-router-dom
  //let [style, setStyle] = useState({diplay:'block'});
  let [buttonShow, setButtonShow] = useState(true);


  return (
    // eslint-disable-next-line
    <div className="App">
    <Navbar bg="light" variant="light">
    <Container>
    <Navbar.Brand>ShoesShop</Navbar.Brand>
    <Nav className="me-auto">
      {/* <Nav.Link href="#home">Home</Nav.Link>
      <Nav.Link href="#features">Cart</Nav.Link>
      <Nav.Link href="#...">...</Nav.Link> */}
      {/* <Link to="/" className='px-3'>Home</Link>  */}
      <Nav.Link onClick={()=>{navigate('/')}} className='px-3'>Home</Nav.Link> 
      {/* <Nav.Link onClick={()=>{navigate('/detail')}} className='px-3'>Detail</Nav.Link>  */}
      <Nav.Link onClick={()=>{navigate('/about')}} className='px-3'>About</Nav.Link>
      {/* <Link to="/about" className='px-3'>About</Link> */}
      {/* <Nav.Link onClick={()=>{navigate(-1)}} className='px-3'>H.Back</Nav.Link> 
      <Nav.Link onClick={()=>{navigate(1)}} className='px-3'>H.Next</Nav.Link>  */}
    </Nav>
    </Container>
  </Navbar>
  
<Routes>
  <Route path="/" element={
    <>
     <div className='main-bg' style={{backgroundImage : 'url('+bg+')'}}></div>
     <div className="container">
  <div className="row">    
  { shoes.map((a,i)=>{
    
    //   
    return (<Col shoes={shoes[i]} i={i} key={i}></Col>);
  } )}
  </div> {/* row */}
  </div> {/* container */}

  { buttonShow ? 
  <button onClick={()=>{
    // ' '     3    
    //    data2.json,    data3.json,    data4.json 
    // 2.json, 3.json, 4.json, 5.json, 6.json, 7.json, 8.json, 9.json, 10.json
    // 2,3,4,5,6,7,8,9,10~ i=+2,
    //   shoes 
    // shoes   setShoes 
    // json data   3  
    console.log('@@ setI(i+1) i=',i);
    axios.get('https://codingapple1.github.io/shop/data' +i+ '.json')
    .then(res=>{
      console.log('@@ res=',res);
      console.log('@@ res.data=',res.data);
      let newData = res.data;
      console.log('@@ console.dir(newData)=');
      console.dir(newData);

      let copy=[...shoes];
      //let copy=[...shoes,...res.data];
      console.log('@@ copy[...shoes]=',copy);
      // copy.push(newData);
      // console.log('@@ copy.push(newData)=',copy);
      newData.forEach(item=>{
        copy.push(item);
        console.log('@@ copy.push(item) ##item=',item);
      })
      console.log('@@ copy.push(item)=',copy);


      setShoes(copy);
      console.log('@@ setShoes(copy) shoes=',shoes);
      //      i +1
      setI(i+1);
      console.log('@@ setI(i+1) i=',i);

    })
    .catch(err=>{
      console.log('@@ err=',err);
    })
    if(i===last){
      setButtonShow(false); //   
    }

  }} > </button>

 : null}
<p> </p><p> </p>
    </>
  } />


  <Route path="/detail/:id" element={<Detail shoes={shoes} />} />
  <Route path="/about" element={<About />}> 
    {/*   UI  . */}
    {/* nested route */}
    <Route path="member" element={<Member />} />
    <Route path="location" element={<Location />} />
  </Route>
  <Route path="/event" element={<Event />}>
    {/* Outlet */}
    <Route path="one" element={<One />} />
    <Route path="two" element={<Two />} />
  </Route>
  {/* <Route path="/about/location" element={<Location />} /> */}
  <Route path="*" element={<div> </div>} />
  {/* <Route path="/contact" element={<Contact />} />
  <Route path="/shoes" element={<Shoes />} /> */}
</Routes>
</div> // end of App
); // return
} // end of App

function Col(props) {
  return (
    // Receive shoes data from props
    // Receive i from props
    console.log('@@ props.shoes=',props.shoes),
    console.log('@@ props.i=',props.i),    
    // .map(item,i),   
    // item.id, item.title, item.content,...,
    <div className="col-12 p-1 col-sm-2 p-sm-2 col-md-4 p-md-4" key={props.i}>
    {/* when using local public folder : img image folder */}
    {/* <img src={process.env.PUBLIC_URL+'/img/shoe'+(props.shoes.id+1)+'.jpg'} className="img-thumbnail" alt="..." width="80%"></img> */}
    <img src={'https://codingapple1.github.io/shop/shoes'+(props.shoes.id+1)+'.jpg'} className="img-thumbnail" alt="..." width="80%"></img>
    <div className="pt-3 pb-3 text-center">
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content}</p>
      <p>{props.shoes.price}</p>
      <Link to={'/detail/'+props.shoes.id} className='px-3'> </Link>
      </div>
    </div>
  ) // end of return
} // end of Col

function Event() {
  return (
    <div>
      <h1> </h1>
      <Outlet></Outlet>
    </div>
  )
} // end of Event

function One() {
  return (
    <div>
      <p>  </p>
    </div>
  )
} // end of One 

function Two() {
  return (
    <div>
      <p> </p>
    </div>
  )
} // end of two

function About() {
  return (
    <>
    <h1>About</h1>
    Lorem ipsum dolor sit amet, consectetur adipiscing elit.
    <Outlet></Outlet>

    </>
  )
}

function Home() {
  return (
    <>
     <h1>Home</h1>
     </>
  )
}
function Member() {
  return (
    <>
     <h1>Member</h1>
     <p>Our member is ...</p>
     </>
  )
}
function Location() {
  return (
    <>
     <h1>Location</h1>
     <p>Our location is ...</p>
     </>
  )
}



export default App;
